const initialState = {
    color: {
        hex: "#d61919",
        rgb: {
            r: 214,
            g: 25,
            b: 25,
            a: 1
        }
    }, savedColors: []
};

export default (c = initialState, action) => {
    switch (action.type) {
        case 'SET_COLOR':
            return { ...c, ...action.payload }
        case 'SET_SAVED_COLORS':
            return { ...c, ...action.payload }
        default:
            return c;
    }
}