import { useSelector } from "react-redux";
import ColorCard from "./colorCard";
import ColorsHeader from "./colorsHeader";
///https://htmlcolorcodes.com/color-chart/web-safe-color-chart/
const SavedColors = () => {
  const { savedColors } = useSelector((state) => state.colorReducer);
  return (
    <div>
      <div>
        <table className="colors-table">
          <tr>
            <th className="saved-colors_table-color">COLOR</th>
            <th className="saved-colors_table-name">NAME</th>
            <th className="saved-colors_table-hex">HEX</th>
            <th className="saved-colors_table-rgb">RGB</th>
          </tr>
          {savedColors.map((element) => (
            <ColorCard color={element} />
          ))}
        </table>
      </div>
    </div>
  );
};

const color = {
  hsl: {
    h: 0,
    s: 0.9675347702610501,
    l: 0.67965728,
    a: 1,
  },
  hex: "#fc5e5e",
  rgb: {
    r: 252,
    g: 94,
    b: 94,
    a: 1,
  },
  hsv: {
    h: 0,
    s: 0.6264,
    v: 0.9895999999999999,
    a: 1,
  },
  oldHue: 0,
  source: "hsv",
};

export default SavedColors;
