import { addDoc, collection, doc, getDocs } from "@firebase/firestore"
import { db } from "../firebase/firebase"

const setColorAction = (color) => {
    return {
        type: "SET_COLOR",
        payload: { ...color }
    }

}

const startSaveColor = () => {
    ///todo
    return (dispatch, getState) => {
        const state = getState();
        const uid = state.authReducer.uid;
        const color = state.colorReducer.color;
        try {
            const docRef = addDoc(collection(db, `users/${uid}/colors`), color);

            dispatch(setSavedColors([...state.colorReducer.savedColors, {id:docRef.id,data:color}]))

        }
        catch (e) {
            console.log(e);
        }
    }
}
const startGetColors = () => {

    return (dispatch, getState) => {
        const state = getState();
        const uid = state.authReducer.uid;

        getDocs(collection(db, `users/${uid}/colors`))
            .then(snapshot => {
                const colors = [];
                snapshot.forEach(item => colors.push({ id: item.id, data: item.data() }));

                dispatch(setSavedColors(colors));
            });
    }
}

const setSavedColors = (colors) => {
    return {
        type: "SET_SAVED_COLORS",
        payload: { savedColors: colors }
    }
}
export { setColorAction, startGetColors, startSaveColor, setSavedColors }