const ColorCard = ({ color, ...rest }) => {
  return (
    <tr>
      <td>
        <div
          style={{ background: color.data.hex, minHeight: 39, maxWidth: "298px" }}
        ></div>
      </td>
      <td>Proba</td>
      <td>{color.data.hex}</td>
      <td>
        {"rgb(" + color.data.rgb.r + "," + color.data.rgb.g + "," + color.data.rgb.b + ")"}
      </td>
    </tr>
  );
};

export default ColorCard;
